<template>
  <div class="component py-4">
    <h5 class="component__heading pt-0 pt-lg-2 px-3 px-lg-5">Withdraw from your Account</h5>
    <div class="row">

      <div class="col-lg-9 col-xl-7">
          <span style="color: #8cc509; font-style: italic;" class="pt-1 mb-2" v-if="authUser.account.main < 70">
              The amount in your account is not sufficient for you to make a withdrawal.
          </span>
        <BetForm class="px-3 px-lg-5">
          <form @submit.prevent="withdrawCash()">
            <div class="form-group">
              <div class="input-group">
                <div class="input-group-prepend">
                  <div class="input-group-text" id="enterAmount">Ksh</div>
                </div>
                <input type="number" class="form-control"
                        v-model="amount"
                        required
                        id="amount"
                       aria-describedby="enterAmount"
                       :placeholder="authUser.account.main < 85? 'Insufficient funds in your account' : 'Enter the Amount'"
                       :disabled="authUser.account.main < 85">
              </div>
              <span style="color: #8cc509; font-style: italic;" class="pt-1" v-if="amount < 70"> At least Ksh. 70, all Mpesa charges apply</span>
            </div>
            <div class="form-group">
            </div>

            <div class="form-group d-flex justify-content-between">
              <BetButton type="submit" :disabled="amount < 70" v-if="!isLoading">Withdraw Cash</BetButton>
              <LoadingIndicator v-if="isLoading"></LoadingIndicator>
              <BetButton @click="$router.push({name: 'sambaza-stake'})">Sambaza Stake</BetButton>
            </div>

          </form>
        </BetForm>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import {CUSTOMER_WITHDRAW_URL} from '../../api_routes'
  import {mapGetters} from 'vuex'
  import {AUTH_CONFIG} from '../../auth_config'
  import BetButton from '../Utilities/BetButton'
  import BetForm from '../Utilities/BetForm'
  import LoadingIndicator from "../Utilities/LoadingIndicator";

export default {
    name: 'Withdraw',
    components: {LoadingIndicator, BetButton, BetForm},
    data () {
      return {
        submitted: false,
        amount: '',
        accountType: '',
        withdrawCashResponse: [],
        errors: [],
        isLoading: false
      }
    },
    computed: {
      ...mapGetters(['authUser'])
    },
    methods: {
      withdrawCash () {
        this.isLoading = true;
        let currentBalance = this.authUser.account.main
        let postData = {
          amount: this.amount,
          msisdn: this.authUser.name,
          owner_id: this.authUser.owner_id,
          account: 'main'
        }
        axios.post(CUSTOMER_WITHDRAW_URL, postData, {headers: AUTH_CONFIG.getHeaders()}).then((response) => {
          let withdrawResponseData = response.data
          if (withdrawResponseData.code === 200) {
            this.$noty.success('Withdraw Successful')
            this.isLoading = false;
            this.$store.dispatch('init_auth_user', {request: 'refresh'})
            this.$router.push({name: 'home'})
          }
        }, (error) => {
          let errorResponse = error.response.data
          this.isLoading = false
          this.amount = ''
          if (errorResponse.status_code === 429) {
            this.$noty.error('Unable to process your request, please try again after 5 minutes')
          } else {
            this.$noty.error('We are unable to process your withdrawal, please try again later, or call our customer care.')
          }
          this.withdrawCashResponse.push(error.response.data.errors)
        })
      }
    }
  }
</script>

<style scoped lang="stylus">
  .component
    background-image url(/img/bg-withdraw.png)
    background-repeat no-repeat
    background-position right 36px bottom 20px
    background-size 25% auto
    min-height 400px

    @media (max-width 1024px)
      background-position right 25px bottom 25px
      background-size 20% auto
      min-height 240px

    @media (max-width 768px)
      background-image none
      min-height 0
</style>
